@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
:root{
    --primary: #2F80ED;
    --primary-light: #79b2fa;
    --primary-dark: #2572d6;
    --btn-shadow: rgba(127, 177, 243, 0.4);
    --gray: #BDBDBD;
    --dark-gray: #828282;
    --white: #f3f3f3;
    --red: #EB5757;
    --red-light: #f56e6e;
    --red-dark: #d73939;
    --fonts: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif
}

body {
    font-size: 16px;
    margin: 0;
    font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-family: var(--fonts);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#root {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

:root{
    --li-size: 89px
}
nav{
    width: 100%;
}

nav ul{
    padding: 0;
    display: -webkit-flex;
    display: flex;
    list-style-type: none;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

nav li{
    font-size: 1rem;
    font-weight: 600;
    width: 89px;
    width: var(--li-size);
    text-align: center;
}

nav li.selected::after{
    content: '';
    display: block;
    position: relative;
    width: 89px;
    width: var(--li-size);
    height: 4px;
    background-color: var(--primary);
    border-radius: 4px 4px 0 0;
    bottom: -16px;
}

hr{
    background-color: var(--gray);
    height: 1px;
    border: none;
}
form *{
    outline: unset;
}

.add-item{
    margin: 18px 0 33px 0;
    width: 100%;
}

.add-item input{
    font-family: var(--fonts);
    font-size: 1rem;
    font-weight: 100;

    width: calc(82% - 25px);

    margin-right: 25px;
    padding: 20px 12px;

    border: 1px solid var(--gray);
    box-sizing: border-box;
    border-radius: 12px;

}

.add-item input::-webkit-input-placeholder{
    color: var(--dark-gray);
}

.add-item input:-ms-input-placeholder{
    color: var(--dark-gray);
}

.add-item input::placeholder{
    color: var(--dark-gray);
}

.add-item input:hover {
    border-color: var(--primary);
}

.add-item input:focus{
    border-color: var(--primary-light);
    box-shadow: 0 0 10px 0 var(--primary-light);
}

.add-item input:disabled{
    border-color: var(--gray);
}

.add-item button{
    font-family: var(--fonts);
    font-weight: 600;

    width: 18%;
    height: 100%;
    background-color: var(--primary);
    box-shadow: 0 2px 6px var(--btn-shadow);

    color: var(--white);
    border-width: 0;
    border-radius: 12px;
}

.add-item button:hover{
    background-color: var(--primary-dark)
}

.add-item button:active{
    background-color: var(--primary-light)
}

.add-item button:disabled{
    background-color: var(--primary-light)
}
#main-container{
    margin: 0 5px;
    width: 600px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

#app-title{
    font-size: 2rem;
    letter-spacing: 0;
    margin: 27px 0;
}

