#delete-all-btn:focus{
    outline: 0;
}

#delete-all-btn img{
    position: absolute;
    top: 2px;
    left: 2px;
    /*margin-right: 2px;*/
}
.todo-item-container{
    margin-bottom: 27px;
    width: 100%;
    display: grid;
    grid-column-gap: 11px;

    grid-template-columns: 20px 1fr 42px;
    grid-template-rows: -webkit-max-content;
    grid-template-rows: max-content;
    -webkit-justify-content: space-between;
            justify-content: space-between; /*?*/
    -webkit-align-content: start;
            align-content: start;
    grid-template-areas:
    'todo-checkbox todo-text todo-icons';
}

.todo-checkbox{
    grid-area: todo-checkbox;
}

.todo-text{
    grid-area: todo-text;
    font-size: 1.2rem;
    margin: 0;
    overflow: hidden;
}

.todo-icons{
    grid-area: todo-icons;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
}
